import {Itemsele,CarousItem} from './Home';
import s1 from './imgs/s1.jpg';

import s3 from './imgs/s3.jpg';
import s4 from './imgs/s4.jpg';
import s5 from './imgs/s5.jpg';
import s6 from './imgs/s6.jpg';
import s7 from './imgs/s7.jpg';
import s8 from './imgs/s8.jpg';
import wp5 from './imgs/wp5.jpg';
import ny from './imgs/ny.jpg';
import first from './imgs/first.jpg';

 
export function Services(){

 return(

<div>
  <ServiceForms />
  <Itemsele />
  </div>
);
}


export function ServiceForms(){
  let requestButton=<button style={{borderRadius:"18px"}} type="text" class="btn btn-success hovereffect"><span class="fa fa-envelope"></span> Send  Message</button>
  //let requestButton=<div style={{borderRadius:"18px"}} class="btn btn-success">Closed. Will be open at 5:00pm</div>
 
  return (
    <div>
    <p></p>  
    <div style={{fontSize:"27px",color:"grey",textAlign:"center",fonyWeight:"bold"}}>Send Message To Kayas</div>
    <div style={{padding:"10px",color:"grey",textAlign:"center",fontFamily:"charm",fontSize:"18px"}}><div style={{fontSize:""}}>Sending messages to Kayas in order to receive services like loans, buy items or sell items can only be possible when you have registered with Kayas Makerere and  recommended a friend as well.</div>
    
    
    
    </div>
    <hr></hr>
   <div style={{padding:"30px"}}>  
   <div style={{color:"green", fontSize:"24px",fontWeight:"bold"}}>REGISTRATION FORM </div>
   <form method="post" action="/collection_kayasers_register">
    <div class="mb-3">
    <input type="text" class="form-control" autoComplete="off" name="name" placeholder='Your Name e.g. Charles Kahuma' required></input>
  <br></br>
  <input type="text" class="form-control" autoComplete="off" name="stdNo" placeholder='Student Number' maxLength={10} minLength={10} required></input><br></br>
    <input type="text" class="form-control" autoComplete="off" name="contact" placeholder='WhatsApp Contact e.g 0703852178' maxLength={10} minLength={10} required></input>
  <br></br> <div style={{fontSize:"12px",color:"green",textAlign:"center"}}>You will receive some quick sale updates through your E-mail</div>
  <input type="text" class="form-control" autoComplete="off" name="email" placeholder='Your E-mail address' required></input>
 
  <br></br>
    <input type="text" class="form-control" autoComplete="off" name="pin" placeholder='Create your PIN e.g. 12345 (5 digits)' maxLength={5} minLength={5} required></input>

  
  
    </div>
    <div style={{fontSize:"12px",color:"green",textAlign:"center"}}>Make sure you have saved the Kayas Business Number 0703852178 before registering.</div>
    <button style={{borderRadius:"18px"}} type="submit" class="btn btn-success hovereffect"><span class="fa fa-user-circle"></span> Register</button>
    </form></div>
    <hr></hr>
  
    <div style={{padding:"30px"}}>  
    <div style={{color:"green",fontWeight:"bold", fontSize:"24px"}}>RECOMMENDATION FORM</div>
    <div style={{fontFamily:"charm",color:"grey",textAlign:"center"}}>Recommend a friend. He/She should after register with Kayas Makerere for you to be able to send a message through the Message Form. <br></br>Incase you have ever recommended a friend, you don't need to recommend again, just send your message through the Message Form found below the Recommendation Form</div>
    <br></br><form method="post" action="/collection_recommendations_recommendation">
    <div class="mb-3">
  
    <input type="text" class="form-control" autoComplete="off" name="recommender" placeholder='Your WhatsApp Contact e.g 0703852178' maxLength={10} minLength={10} required></input>
  <br></br>
  <div style={{fontSize:"12px",color:"green",textAlign:"center"}}>Enter the WhatsApp number of your friend you wish to recommend below:</div>
  <input type="text"  class="form-control" autoComplete="off" name="recommendee" placeholder='Whom To Recommend e.g 0703852178' maxLength={10} minLength={10} required></input>
  <br></br>
  
  
    <input type="text" class="form-control" autoComplete="off" name="pin" placeholder='Enter Your PIN' maxLength={5} minLength={5} required></input>
    </div>
    
    <button style={{borderRadius:"18px"}} type="text" class="btn btn-success hovereffect"><span class="fa fa-user-circle"></span> Recommend</button>
    </form></div>
    <hr></hr>
    
    <div style={{padding:"30px"}}>  
    <div style={{color:"green",fontWeight:"bold", fontSize:"24px"}}>MESSAGE FORM</div>
    <form method="post" action="/collection_requests_service">
    <div class="mb-3">
  
    <input type="text" class="form-control" autoComplete="off" name="contact" placeholder='WhatsApp Contact e.g 0703852178' maxLength={10} minLength={10} required></input>
  <br></br>
  
  <textArea rows="5"type="text" list="serviceTypes" class="form-control" autoComplete="off" name="serviceType" placeholder='Describe Your Request e.g. need a Loan or Sell item or Purchase item, etc' required></textArea>
  <br></br>
  
  <datalist id="serviceTypes">
    
  <option value="Purchase Item (Name of the item?)"></option>
  <option value="Sell Item (Name of the item?)"></option>
  <option value="Loan"></option>
  
  </datalist>
  
    <input type="text" class="form-control" autoComplete="off" name="pin" placeholder='Enter Your PIN' maxLength={5} minLength={5} required></input>
    </div>
    
    {requestButton}
    </form></div>
    <hr></hr>
  
  
  <Itemsele />
    
    </div>)
}
export function Carousele(){
  let caroushght="350px";
  return (
       <div id="carouselExampleControls" class="carousel slide carousel-slide col-md-12" data-bs-ride="carousel">
       <div class="nega"><strong>Keep It Kayas</strong></div>
      <div class="carousel-inner">
      <div className="row"> 
         <div className="col-1"></div>
         <div class="col-10">
      <div class="carousel-item active">
<img src={wp5} class=" d-block w-100" alt="..." height={caroushght} /><div class="carousel-caption caption-four  d-md-block">
<br></br><br></br><br></br><div><div class="carmsgcolor">Please ask the person who shared the opportunity to you to send your contact to us</div></div>

 </div>
   </div>
   <CarousItem hght={caroushght} img={s6} msg="Please ask the person who shared the opportunity to you to send your contact to us" />
   <CarousItem hght={caroushght} img={s6} msg="Thank you Julia for your support" />
   <CarousItem hght={caroushght} img={s3} msg="Mubs we got you covered" />
      <CarousItem hght={caroushght} img={s1} msg="Muk you are sorted" />
   <CarousItem hght={caroushght} img={s5} msg="" />
   <CarousItem hght={caroushght} img={s3} msg="Kyu at your service" />
   <CarousItem hght={caroushght} img={s4} msg="Wishing you the best of 2022" />
   <CarousItem hght={caroushght} img={s7} msg="Delicious" />
   <CarousItem hght={caroushght} img={s8} msg="Discount for students" />
   <CarousItem hght={caroushght} img={first} msg="We offer students discounts" />
   <CarousItem hght={caroushght} img={ny} msg="Dont forget to buy some for yourself" />
   <CarousItem hght={caroushght} img={ny} msg="Is it a birthday party? Contact us" />
   <CarousItem hght={caroushght} img={first} msg="Send your orders and request to Kayas OI. WhatsApp 0703852178" />
  <CarousItem hght={caroushght} img={ny} msg="Happy New 2022" />
  
  </div>
     <div className="col-1"></div>
     </div>
  
 
  
  

   </div>
    <button class="carousel-control-prev" type="button" data-bs-target="#carouselExampleControls"  data-bs-slide="prev">
    <span class="carousel-control-prev-icon" aria-hidden="true"></span>
    <span class="visually-hidden">Previous</span>
  </button>
  <button class="carousel-control-next" type="button" data-bs-target="#carouselExampleControls"  data-bs-slide="next">
    <span class="carousel-control-next-icon" aria-hidden="true"></span>
    <span class="visually-hidden">Next</span>
  </button>
  </div>
  );
}







export default Services