import React, {useEffect,useState} from 'react'
import {ThemeWrap} from './Home';
import kahu1 from './imgs/kahu1.jpg'; 
import bhazi from './imgs/bhazi.jpg'; 
import wobu1 from './imgs/wobu1.jpg';
import holiday from './imgs/holiday.jpg';
export function Quotes(){

  const [quotes,setQuotes]=useState('')
  const [refName,setRefName]=useState('')
  const [writersMsg,setWritersMsg]=useState('')
  const [writersName,setWritersName]=useState('')

  useEffect(()=>{
 
let data=""
  fetch('/collection_quotes_quotes').then(res=>res.json()).then(res=>{
   res.reverse()
    res.forEach(quote=>{
   
      data+='<div>'+quote.quote+"</div><hr></hr>"
  
    })
   setQuotes(data)
      })

      fetch('/collection_controls').then(res=>res.json()).then(res=>{
        setWritersName(res[0].writersName)
        setWritersMsg(res[0].writersMsg)
        setRefName(res[0].refName)
      })


  
  },[])



//<ThemeWrap img={t14}headline="" date="Friday 8th July, 2022" msg=""/>

return(

  <div>
    
    <div style={{padding:"5px",color:"red",background:"white"}}><span style={{fontSize:"50px"}}>D</span><span style={{fontSize:"30px"}}>ear, </span><span style={{fontSize:"30px"}}>{refName}</span></div>
    <div style={{fontSize:"20px",color:"grey",textAlign:"center",fontFamily:"charm",fonyWeight:"bold"}}>As you read these quotes, quote yourself too<p></p></div>
    <ThemeWrap img={holiday}headline={writersName} date="Through Kayas Makerere" msg={writersMsg}/>
    
 <div style={{textAlign:"center",padding:"30px",fontSize:"15px",fontFamily:"charm"}}><div dangerouslySetInnerHTML={{__html:quotes}}/></div>
 <div style={{fontSize:"20px",color:"red",textAlign:"center",fonyWeight:"bold",paddingBottom:"15px"}}><div style={{fontSize:"30px",fontWeight:"bold"}}>Who is Kayas? </div><div style={{fontFamily:"charm"}}>Get to know facts from those who know him better.<div ><a style={{color:"green"}}href="https://twitter.com/isaacopiokayas"><span class="fa fa-twitter hovereffect"> Follow Kayas on Twitter</span></a></div></div></div>
 <ThemeWrap img={kahu1} headline="Charles" date="0700411626" msg="Opio Isaac is a man of his own
He has alot of manly qualities and one that Ive noticed over the years is his huge ego that makes him want most of the things or if not everything be done his way, 
He is very good at explaining in detail. Opio Isaac loves seeing people happy, he is a very generous person"/>
<ThemeWrap img={bhazi}headline="Bhazi" date="0784586870" msg="Opio Isaac is a smart, bright, quick learner, and above all, a sought-after wiseacre. Few people know the true meaning of the nickname 'Kayas,' which has become well-known and a household brand.I really appreciate and acknowledge all of your efforts on my behalf when we first met nine years ago. If you know him, you'll understand why I refer to him as an 'all-the rounder'it's most accurate description. Many blessings and success"/>
<ThemeWrap img={wobu1}headline="Davis" date="0701754108" msg="Opio Isaac,  has always thought outside the box, a man of integrity and intelligence,  he has been their for the friends in each and every hardship they have gone through,  he has always  wanted the best for us, ever since we were in high school, I always pray God grants him his needs and wants"/>



 </div>
);}





export default Quotes