function Art(){
  let data=""
 
  const [dis,setDis]=useState('')
  const [comNumb,setNumb]=useState('')

  useEffect(()=>{
   

  fetch('/collection_campus_comments').then(res=>res.json()).then(res=>{
    setNumb(res.length)
    res.forEach(comment=>{
   
      data+='<div>'+comment.name+" "+comment.contact+"</div><div>"+comment.body+"</div><hr></hr>"
    
    })
    
    setDis(data);
      })
  
  },[])




  

  
  return (
    
     
      <div style={{background:"white"}}class="row">
        
       <div style={{textAlign:"center"}}>  <div style={{fontWeight:"bold",fontSize:"20px",color:"red",paddingTop:"10px"}}>What's at Campus? </div> </div>
     
      
       <p></p><div style={{textAlign:"center"}}>  <div style={{fontWeight:"",color:"grey",padding:"4px"}}>Looking at the experience you have had from when you were born till today, all the challenges, successes and failures that you have gone through. If you were returned into your mother's womb as a baby and asked the question below. <br></br><span style={{color:"red"}}>Question: Do you want to be born to the world again or not?</span> <p></p>What would be your reply? Add your comment through the Form Below.</div> <p></p></div>
     <div style={{textAlign:"center",padding:"20px"}}> 
    
      <form method="post" action="/collection_campus_comment">
  <div class="mb-3">
 

  <input type="text" class="form-control" autoComplete="off" name="name" placeholder='Your Name e.g. Charles'   required></input>
<br></br>
  <input type="text" class="form-control" autoComplete="off" name="tel" placeholder='Enter Your WhatsApp Contact'  minLength={10} maxLength={10} required></input>
<br></br>

    <textarea rows="5"  type="text" class="form-control" autoComplete="off" name="msg" placeholder='Type Your Message Here' required></textarea>
    <br></br>
    <input type="text" class="form-control" autoComplete="off" name="pin" placeholder='Enter Your PIN' maxLength={5} required></input>
  </div>

  
  <button type="submit" class="btn btn-success">Add Your Post</button>
</form>
      
      
</div>

<div style={{textAlign:"center",fontWeight:"bold",color:"red"}}>{comNumb} Posts below:</div>
<div style={{textAlign:"center",padding:"20px",fontSize:"15px",fontFamily:"charm"}}><div dangerouslySetInnerHTML={{__html:dis}}/></div>
  
     </div>
  );  
}





export function ArtCompRead(props){
  let v="col-12 col-sm-12 col-md-6",phone=256703852178,link="https://api.whatsapp.com/send?phone="+props.phone+"&text=Hello%20"+props.name+",%20my%20comment%20about%20your%20article%20on%20Kayas%20Makerere%20University%20is;%20"
  return (

<div class={v} style={{paddingBottom:"10px"}}><p></p> <div style={{padding:"10px"}}><div className="row"> <div style={{textAlign:"center",paddingTop:"30px"}}class="col-6"><span style={{fontWeight:"bold",fontSize:"15px", color:"green"}}>{props.name}</span> <span style={{fontWeight:"",textAlign:"center",fontSize:"15px", color:"rgb(187, 9, 9)"}}>{props.post}</span>  <div> <a href={link}><button type="button" class="btn btn-sm btn1 btn-success"> <span class="fa fa-whatsapp"> </span> Your Comment?</button></a></div> </div> </div></div> <div style={{paddingLeft:"5px"}}>    <div  style={{color:"grey",textAlign:"center",fontFamily:"Charm",border:"0.5px solid grey",borderRadius:"20px",padding:"3px",overflow:"scroll"}}>{props.msg}</div>    </div></div>

  );
}




export function Theme(){
  let v="col-sm-12 col-md-4 gencols";
  
  return (
      <div class="row">
      <div style={{textAlign:"center",fontFamily:"charm", fontWeight:"bold",color:"red"}}>Queen Elizabeth II dies at 96 years </div>
        <ThemeWrap img={camp1}headline="" date="Thursday 8th Sept, 2022" msg=""/> 
      
     </div>
  );
}





export default Campus